// ==========================================================================
// Base Styles
// ==========================================================================
* {
    box-sizing: border-box;
}

.wrapper {
    padding: 20px;
    margin: 0 auto;

    @media (min-width: $brkpoint-sm) {
        max-width: $brkpoint-sm;
    }
    @media (min-width: $brkpoint-md) {
        max-width: $brkpoint-md;
    }
    @media (min-width: $brkpoint-lg) {
        max-width: $brkpoint-lg;
    }
}