// ==========================================================================
// Nav Layout
// ==========================================================================

.nav {
    display: none;
    background-color: $color-nav-bg;
    color: $color-white;

    @media (min-width: $brkpoint-md) {
        display: block;
    }

    .flex-container {
        display: flex;
        justify-content: space-around;
        margin: 0;
        padding-left: 0;
    }

    i {
        padding-bottom: 10px;
        font-size: 1.2em;
    }

    .software {
        color: $color-software--light;
    }
    .support {
        color: $color-support--light;
    }
    .marketing {
        color: $color-marketing--light;
    }
    .tservices {
        color: $color-services--light;
    }
    .design {
        color: $color-design--light;
    }
    .security {
        color: $color-security--light;
    }
    .course {
        color: $color-course--light;
    }


    li {
        text-align: center;
        list-style-type: none;
        ul {
            padding-left: 0;
            text-transform: uppercase;
            :first-child {
                font-size: 0.8em;
            }
            :last-child {
                font-size: 1.2em;
            }
        }
    }


}